













import Heart from "@/components/Heart.vue";

export default {
  name: 'home',
  components: {Heart},
  methods: {
    startConfetti() {
      //@ts-ignore
      this.$confetti.start()
      this.love()
      //@ts-ignore
      this.circle = true
      //@ts-ignore
      this.heartbeat = false
      //@ts-ignore
      this.percentage = Math.floor(Math.random() * 100)
      //@ts-ignore
      console.log(this.circle)
    },
    stopConfetti() {
      //@ts-ignore
      this.$confetti.stop()
    },

    love() {
      //@ts-ignore
      this.$confetti.update({
        particles: [
          {
            type: 'heart',
          }
        ],
        defaultColors: [
          'red'
        ],
        defaultSize: 15,
        defaultDropRate: 5,
        particlesPerFrame: 0.2
      });
    }
  },
  data: function () {
    return {
      percentage: 0,
      heartbeat: true,
      circle: false,
      name: ""
    }
  },
  created() {
  }
};
